// Styles for Work page

 @import "../base/base"; 


.site-main{
    
    .page-header{
        text-align: center;
    }

    .work{
        text-transform: uppercase;
        font-weight: $fontBold;
       
    }

    .project{
        border: 1px solid black;
        box-shadow: 8px 8px 17px 2px rgba(0,0,0,0.75);
        -webkit-box-shadow: 8px 8px 17px -2px rgba(0,0,0,0.75);
        -moz-box-shadow: 8px 8px 17px -2px rgba(0,0,0,0.75);
        padding: 30px;
        margin: 0 1rem;
        margin-bottom: 30px;

        .work-title{
            text-transform: uppercase;
            text-align: center;
            font-weight: $fontSemi;
            margin: 0px;
        }    

    
        .tools{
            font-weight: $fontReg;
            text-align: center;        
        }

        @media (min-width: 350px) {
            @include mc;
            max-width: 330px;
            // padding: 0.8rem;
            margin-bottom: 3rem;
        }
        @media (min-width: 410px) {  
            max-width: 360px;
            padding-right: 2rem;
            padding-left: 2rem;  
        }   
        
        @media (min-width: 550px){
            max-width: 500px;
            
        }
        
        @media (min-width: 700px){
            max-width: 600px;
        }    
        
        @media (min-width: 800px){
            max-width: 700px;
        }

        
    }// end project
    
    
}




    
    

     
    